.page-header {
  width: 100%;
  background-color: #1b2d37; }

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  width: 76%;
  margin: 0 auto; }

.page-header__logo-link {
  margin-top: 33px;
  margin-right: 10px;
  margin-bottom: 10px;
  margin-left: 23px; }

.page-header__contact-item:hover,
.page-header__logo-link:hover,
.page-footer__logo-link:hover {
  opacity: 0.8; }

.page-header__contact-item:active,
.page-header__logo-link:active,
.page-footer__logo-link:active {
  opacity: 0.6; }

.page-header__contact-wrapper {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  flex-grow: 1;
  flex-wrap: wrap;
