  padding-right: 11px;
  padding-bottom: 10px; }

.page-header__contact-item,
.page-header__logo-link {
  text-decoration: none;
  cursor: pointer; }

.page-header__contact {
  line-height: 18px;
  color: #ffffff;
  white-space: nowrap; }

.page-header__contact-item {
  min-height: 25px;
  margin-top: 9px;
  margin-right: 4.1%;
  margin-left: 3.1%;
  background-position: left;
  background-repeat: no-repeat; }

.page-header__contact-item--email {
  padding-left: 39px;
  background-image: url("../img/icon-envelope.svg");
  background-size: 19px; }

.page-header__contact-item--phone {
  padding-left: 35px;
  background-image: url("../img/icon-phone.svg");
  background-size: 18px; }

@media (max-width: 1200px) {
  .page-header__wrapper {
    width: 90%; } }

@media (max-width: 1024px) {
  .page-header__contact {
    display: none; }
  .page-header__contact-item--email {
    background-size: 25px; }
  .page-header__contact-item--phone {
    background-size: 24px; }
  .page-header__contact-wrapper {
    padding-top: 24px;
    padding-right: 0; }
  .page-header__contact-item {
    margin-top: 7px;
    margin-right: 0; }
  .page-header__wrapper {
    width: 100%; }
  .page-header__logo-link {
    margin-top: 19px;
    margin-left: 25px; } }

@media (max-width: 768px) {
  .page-header__contact-wrapper {
    padding-top: 14px; }
  .page-header__contact-item--phone {
    padding-left: 39px; }
  .page-header__contact-item--email {
    padding-left: 41px; }
  .page-header__logo-link {
    margin-top: 17px;
    margin-left: 17px; } }

.page-footer {
  width: 100%;
  background-color: #fafafa;
  background-image: linear-gradient(to top, transparent 52px, rgba(53, 55, 53, 0.2) 52px, rgba(53, 55, 53, 0.2) 53px, transparent 53px); }

.page-footer__big-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 76%;
  margin: 0 auto;
  padding-top: 65px; }

.page-footer__small-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  min-height: 190px;
  margin-bottom: 10px; }

.page-footer__logo-wrapper {
  display: flex;
