@media (max-width: 1200px) {
  .page-footer__big-wrapper {
    width: 90%; } }

@media (max-width: 1024px) {
  .page-footer {
    background-image: linear-gradient(to top, transparent 73px, rgba(53, 55, 53, 0.2) 73px, rgba(53, 55, 53, 0.2) 74px, transparent 74px), linear-gradient(to bottom, transparent 97px, rgba(53, 55, 53, 0.2) 97px, rgba(53, 55, 53, 0.2) 98px, transparent 98px); }
  .page-footer__big-wrapper {
