.button {
  display: inline-block;
  box-sizing: border-box;
  min-width: 100px;
  margin: 0;
  padding: 13px 26px;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
