body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "PTMono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: #666666;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
}

.content-wrapper {
  max-width: $content-width;
  margin: 0 auto;
}
